import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { ClubInformationRequestData } from 'src/hooks/api/settings'

export type CreateClubInfoRequestData = ClubInformationRequestData & { name: string }
type PostClubParams = CreateClubInfoRequestData

/**
 * 동아리 정보 KLUB에 등록
 */
const postClub = async (clubInfo: PostClubParams, token: string) => {
  await axios.post<null, AxiosResponse<null>, PostClubParams>(`${process.env.REACT_APP_APIHOST}/admin/club`, clubInfo, {
    headers: { Authorization: token }
  })
}

export const useCreateClub = (): UseMutationResult<void, unknown, PostClubParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const mutationFn = (values: CreateClubInfoRequestData) => postClub(values, token)

  return useMutation('postClub', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(error.response?.data?.message ?? COMMON_MESSAGE.ADD_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

export type ClubLeaderRequestData = {
  clubName: string
  studentId: string
  email: string
}

/**
 * 동아리의 회장 정보 등록
 */
const postClubLeader = async (clubLeaderInfo: ClubLeaderRequestData, token: string) => {
  await axios.post<null, AxiosResponse<null>, ClubLeaderRequestData>(
    `${process.env.REACT_APP_APIHOST}/admin/club/superadmin`,
    clubLeaderInfo,
    {
      headers: { Authorization: token }
    }
  )
}

export const useCreateClubLeader = (): UseMutationResult<void, unknown, ClubLeaderRequestData, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const mutationFn = (values: ClubLeaderRequestData) => postClubLeader(values, token)

  return useMutation('postCreateClub', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar('회장으로 등록되었습니다.', {
        variant: 'success'
      })
      enqueueSnackbar('재로그인 시 회장은 본인 동아리 관리를 시작할 수 있습니다.', {
        variant: 'success'
      })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(error.response?.data?.message ?? COMMON_MESSAGE.ADD_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
