import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  type SelectChangeEvent,
  TextField,
  styled
} from '@mui/material'

import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES from 'src/data/categories'

import type { ClubInformationRequestData } from 'src/hooks/api/settings'

interface ClubSettings {
  clubInformation: ClubInformationRequestData & { name?: string }
  handleChange: (e: React.ChangeEvent<HTMLInputElement>) => void
  handleSelect: (e: SelectChangeEvent<string>) => void
  handleClickEditButton: React.MouseEventHandler<HTMLButtonElement>
  fixedClubName?: string
}

const ClubSettingsField: React.FC<ClubSettings> = ({
  clubInformation,
  handleChange,
  handleSelect,
  handleClickEditButton,
  fixedClubName
}) => {
  return (
    <Grid container alignItems="center" mt={2} spacing={2}>
      <ItemGrid item xs={12}>
        <TextField
          fullWidth
          label="동아리명"
          name="name"
          id="name"
          value={clubInformation.name ?? fixedClubName ?? ''}
          disabled={!!fixedClubName}
          helperText={!!fixedClubName ? '동아리명은 수정할 수 없습니다' : ''}
          onChange={handleChange}
        />
      </ItemGrid>
      <ItemGrid item xs={12} sm={6}>
        <TextField
          label="동아리 설명"
          required
          name="description"
          id="description"
          fullWidth
          value={clubInformation.description}
          onChange={handleChange}
        />
      </ItemGrid>

      <ItemGrid item xs={12} sm={6}>
        <TextField
          label="동아리 장소"
          name="location"
          id="location"
          fullWidth
          value={clubInformation.location}
          onChange={handleChange}
        />
      </ItemGrid>
      <ItemGrid item xs={6}>
        <FormControl fullWidth>
          <InputLabel id="belonging-label">소속</InputLabel>
          <Select
            labelId="belonging-label"
            id="belonging"
            name="belonging"
            label="소속"
            value={clubInformation.belonging}
            onChange={handleSelect}
            required
          >
            {CLUB_BELONGING &&
              CLUB_BELONGING.map((name, idx) => (
                <MenuItem key={idx} value={name}>
                  {name}
                </MenuItem>
              ))}
          </Select>
        </FormControl>
      </ItemGrid>
      <ItemGrid item xs={6}>
        <FormControl fullWidth>
          <InputLabel id="category-label">동아리 카테고리</InputLabel>
          <Select
            labelId="category-label"
            id="category"
            name="category"
            label="카테고리"
            value={clubInformation.category}
            onChange={handleSelect}
            required
          >
            {CLUB_CATEGORIES &&
              CLUB_CATEGORIES.map((name, idx) => (
                <MenuItem key={idx} value={name}>
                  {name}
                </MenuItem>
              ))}
          </Select>
        </FormControl>
      </ItemGrid>
      <ItemGrid item xs={12} justifyContent={'flex-end'}>
        <Button variant="contained" color="success" onClick={handleClickEditButton}>
          {fixedClubName ? '수정 / 등록' : '등록'}
        </Button>
      </ItemGrid>
    </Grid>
  )
}

export default ClubSettingsField

const ItemGrid = styled(Grid)({
  display: 'flex'
})
