import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate } from 'react-router'

import { Box, Button, Card, Grid, type SelectChangeEvent, TextField, Typography } from '@mui/material'

import ClubSettingsField from 'src/components/ClubSettingsField'

import {
  type ClubLeaderRequestData,
  type CreateClubInfoRequestData,
  useCreateClub,
  useCreateClubLeader
} from 'src/hooks/api/admin'

function AdminPage() {
  const navigate = useNavigate()

  const createClub = useCreateClub()
  const createClubLeader = useCreateClubLeader()

  const user = useAuthUser()()

  const { enqueueSnackbar } = useSnackbar()

  const [clubInformation, setClubInformation] = useState<CreateClubInfoRequestData>({
    name: '',
    description: '',
    category: '',
    belonging: '',
    location: ''
  })

  const [clubLeaderInformation, setClubLeaderInformation] = useState<ClubLeaderRequestData>({
    clubName: '',
    studentId: '',
    email: ''
  })

  useEffect(() => {
    if (user?.permission !== 'SUPERADMIN') {
      enqueueSnackbar('권한이 없습니다', { variant: 'error' })
      navigate('/mypage')
    }
  }, [user?.permission])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleSelect = (e: SelectChangeEvent<string>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleLeaderInformationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setClubLeaderInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleRegisterClub: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (
      !clubInformation.name ||
      !clubInformation.description ||
      !clubInformation.category ||
      !clubInformation.belonging
    ) {
      enqueueSnackbar('모든 정보를 입력해주세요', { variant: 'warning' })
      return
    }
    createClub.mutate(clubInformation)
  }

  const handleRegisterClubLeader = () => {
    createClubLeader.mutate(clubLeaderInformation)
  }

  const handleNavigateBack = () => {
    navigate('/mypage')
  }

  return (
    <>
      <Helmet>
        <title>KLUB 어드민 페이지</title>
      </Helmet>
      <Card style={{ margin: 20, padding: 20, justifyContent: 'center', alignItems: 'center' }}>
        <Typography variant="h3" component="h4" textAlign={'center'} mb={4}>
          KLUB 어드민 페이지
        </Typography>
        <Box p={4}>
          <Typography variant="h4" component="h4" textAlign={'center'} mb={4}>
            동아리 등록
          </Typography>
          <ClubSettingsField
            clubInformation={clubInformation}
            handleChange={handleChange}
            handleSelect={handleSelect}
            handleClickEditButton={handleRegisterClub}
          />
          <Typography variant="h4" component="h4" textAlign={'center'} mb={4}>
            동아리 회장 등록
          </Typography>
          <Grid container alignItems="center" mt={2} spacing={2}>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="동아리명"
                name="clubName"
                id="clubName"
                value={clubLeaderInformation.clubName}
                helperText={'정확한 동아리명을 입력해주세요'}
                onChange={handleLeaderInformationChange}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="동아리장 학번"
                required
                name="studentId"
                id="club-leader-student-id"
                fullWidth
                value={clubLeaderInformation.studentId}
                onChange={handleLeaderInformationChange}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="동아리장 이메일"
                name="email"
                id="email"
                fullWidth
                value={clubLeaderInformation.email}
                onChange={handleLeaderInformationChange}
              />
            </Grid>
            <Grid display={'flex'} item xs={12} justifyContent={'flex-end'}>
              <Button variant="contained" color="success" onClick={handleRegisterClubLeader}>
                등록
              </Button>
            </Grid>
          </Grid>
        </Box>
        <Box display={'flex'} flex={1} justifyContent={'center'}>
          <Button onClick={handleNavigateBack} color="secondary">
            뒤로 가기
          </Button>
        </Box>
      </Card>
    </>
  )
}

export default AdminPage
